import React from 'react';
import {connect} from 'react-redux';
import Typography from '@mui/material/Typography';
import {getSummary} from '../../reducers/notes';
import Container from '@mui/material/Container';
import Box from '@mui/material/Box';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import Divider from '@mui/material/Divider';
import ListItemText from '@mui/material/ListItemText';
import { LinearProgress } from '@mui/material';
import FormDialog  from './addnotes';
class Items extends React.Component {
    constructor(props) {
        super(props)
    }
    componentDidMount () {
        let timestampe = this.props.notes.currentDate;
        let clientId = this.props.client.clientinfo.id;
        this.props.getSummary({clientId, timestampe});
    }
    render () {
        let summary = this.props.notes.summary.slice(2)
        return (
           <div>
               <Box sx={{marginTop: 8,display: 'flex',flexDirection: 'row', justifyContent:'space-between'}}>
                   <Box sx={{marginLeft:2}}><Typography variant='h5'>{this.props.notes.currentDate}</Typography></Box>
               </Box>
              <Box sx={{display:"flex", flexDirection:"row", justifyContent:"space-evenly", fontFamily:"sans-serif"}}>
               {summary.map((row,index)=>(
                   <Box >{row.actionitems.length >0 ? (
                        <Box>
                            <Typography variant="h5">{row.name}</Typography>
                            {row.actionitems.map((items) => (
                                <ul>
                                    <li>{items}</li>
                                </ul>
                            ))}
                        </Box>
                       ): null}
                   </Box>
               ))}
            </Box>
            <Box sx={{display:'flex', alignItems:'center', justifyContent:'center'}}>
                <FormDialog />
            </Box>
           </div>
        );
    }
}
const mapStateToProps = (state) => ({
    client:state.ClientReducer,
    notes:state.NotesReducer,
})
const mapDispatchToProps = {
    getSummary
}
export default connect(mapStateToProps, mapDispatchToProps)(Items)
